$(document).ready(function() {
        $('.center').slick({
                centerMode: true,
                centerPadding: '0px',
                slidesToShow: 3,
                focusOnSelect: true,
                infinite: true,
                arrows: false,
                responsive: [{
                                breakpoint: 768,
                                settings: {
                                        arrows: false,
                                        centerMode: true,
                                        centerPadding: '40px',
                                        slidesToShow: 3
                                }
                        },
                        {
                                breakpoint: 480,
                                settings: {
                                        arrows: false,
                                        centerMode: true,
                                        centerPadding: '40px',
                                        slidesToShow: 1
                                }
                        }
                ]
        });

        $('button').on('click', function() {
                $('body').toggleClass('open');
        });
});